<template>
  <component :is="mediaLayout">
    <router-view />
  </component>
</template>

<script>
import { useBreakpointsMedia } from '@/plugins/breakpoints'
import { computed, defineAsyncComponent } from 'vue'

export default {
  name: 'DefaultLayout',
  components: {
    MobileLayout: defineAsyncComponent(() => import('@/layouts/MobileLayout')),
    TabletLayout: defineAsyncComponent(() => import('@/layouts/TabletLayout')),
    DesktopLayout: defineAsyncComponent(() => import('@/layouts/DesktopLayout'))
  },
  setup () {
    const {
      isMobile,
      isDesktop
    } = useBreakpointsMedia()

    const layouts = {
      mobile: 'MobileLayout',
      desktop: 'DesktopLayout',
      tablet: 'TabletLayout'
    }
    const mediaLayout = computed(() => {
      if (isMobile.value) {
        return layouts.mobile
      }
      if (isDesktop.value) {
        return layouts.desktop
      }
      return layouts.tablet
    })

    return {
      mediaLayout
    }
  }
}
</script>

